import React, { useEffect, useState } from "react";
import { Avatar, message, Tag, Button, } from "antd";
import styled from "styled-components";
import { getService } from "../../services";
import { navigate } from "@reach/router";
import { URL_S3, URL_CATEGORY_DEFAULT } from "../../constants";
import { useSelector, useDispatch } from "react-redux";


const Wrapper = styled.div`
  & .tag-container{
    margin-top:40px!important;
    margin-bottom:20px!important;
  }
  
  @media(max-width:424px){
    & .tag-container{
      margin-top: 40px!important;
      margin-bottom: 20px!important;
      display: flex;
      justify-content: space-between;
      white-space: nowrap;
      max-width: calc(100% - 20px);
      overflow-x: auto;
      margin: 10px;
    }
    & .tag-container > .ant-tag {
        min-width: auto;
    }
  }
  ${({ configuration }) => {
    return `
    border:${configuration && configuration.primary_color || "2px solid var(--color-primary)"}!important;
    color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
    `;
  }}
`;
const MyTag = styled(Tag)`

  cursor:pointer!important;
  border-radius: 28px !important;
  border: 2px solid !important;
  padding: 8px !important;
  min-width: 80px;
  text-align: center;
  font-size: 14px !important;
  background: var(--color-white) !important;
  color: var(--color-primary) !important;

  font-family: 'Jost';
  font-weight: 600;
  
  font-size: 16px!important;

  text-transform: capitalize!important;
  
  & .ant-avatar {
    margin-right: 8px!important;
  } 
  & .ant-avatar > img {
    object-fit: contain!important;
  }
  ${({ configuration }) => {
    return `
    border-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
    color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
    `;
  }}
`;
const Tags = ({ reference, filterDefaultValues = {}, id, optionText = "name", ...props }) => {
  const [dataSource, setDataSource] = useState([]);
  const [loading, setLoading] = useState(true);
  const handleClick = (id) => {
    navigate(`/pack-categories/${id}`)
  }
  const entity = useSelector(({ auth }) => {
    return auth.entity
  })
  const getData = () => {
    if (reference) {
      const service = getService(reference);
      service.find({
        query: {
          ...filterDefaultValues
        }
      })
        .then(({ data }) => {
          setLoading(false);
          setDataSource(data);
        })
        .catch(err => {
          setLoading(false);
          message.error(err.message);
        });
    } else {
      setDataSource(props.dataSource || [])
    }
  }
  useEffect(() => {
    getData();
  }, [props.dataSource])
  return (<Wrapper configuration={{
    primary_color: entity && entity.primary_color || "var(--color-primary);",
    secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
    text_color: entity && entity.text_color || "var(--color-white)",
  }}>
    <div className="tag-container">
      {dataSource.map((tag, key) => {
        return <MyTag configuration={{
          primary_color: entity && entity.primary_color || "var(--color-primary);",
          secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
          text_color: entity && entity.text_color || "var(--color-white)",
        }}
          key={key} onClick={() => handleClick(tag.id)}>
          <>
            {
              <Avatar
                shape="square"
                size="small"

                src={tag.icon ? `${URL_S3}/${tag.icon} ` : URL_CATEGORY_DEFAULT}
              />
            }

            {tag[optionText]}
          </>
        </MyTag>;
      })}
    </div>
  </Wrapper>
  );
};
export default Tags;
