import React, { useEffect, useState } from "react";
import { Row, Col } from "reactstrap";
import { Wrapper } from "./Styles";
import styled from 'styled-components';
import { navigate } from "@reach/router";
import { URL_BASE_S3 } from "../../constants/";
export const ItemWrapper = styled.div`
    margin:0px;
    cursor:pointer;
    padding-right: 4px!important;
    padding-left: 4px!important;
    padding-top:4px!important;
    
    & .item-container{
        display:block;
        height:100%;
    }
    &.item-thumbnails{
        height: 250px;
        margin: 10px;
    }
    &.item-cover{
        height:520px;
    }

    & .item-container{
        background-repeat:no-repeat;
        background-repeat: no-repeat!important;
        background-size: cover!important;
        background-position: center!important;
        border-radius: 10px;
        overflow: hidden;
        padding: 10px 0px;
        position:relative;
    }
    & span.item-tag {
        position: absolute;
        top: 0px;
        left: 0px;
        padding: 4px 16px;
        line-height: 20px;
        border-radius: 0px 0px 10px 0px;
        color:#fff;
    }
    & .item-title {
        position: absolute;
        bottom: 0px;
        width: 100%;
        background: linear-gradient(360deg, #000000d6, transparent);
        text-align: center;
        color: #fff;
        font-weight: bold;
        padding: 8px 4px;
        
        font: Bold 50px/60px Poppins;
        font-size: 18px!important;
        line-height: 20px!important;

    }
`;
const Grid = ({ dataSource, layout, perColumn = 4, columns = 2, rows = 2, renderItem, ...props }) => {
    const [items, setItems] = useState([]);
    const renderGridLayout = (item, index, arr) => {
        let { children } = item;
        const grid = {
            xl: arr.length == 1 ? 12 : 6,
            xs: 12,
            lg: 6,
            md: 6,
            ...props.grid
        }
        if (children) {
            return (
                <Col xl={12 / columns}>
                    <Row className="children-container">
                        {
                            children.map((it, index, arr) => (renderGridLayout(it, index, arr)))
                        }
                    </Row>
                </Col>
            );
        }
        return (<Col className={`col-children ${arr.length == 1 && "col-cover"}`}  {...grid}>
            {typeof renderItem != "undefined" ? renderItem(item, index, arr) : defaultRenderItem(item, index, arr)}
        </Col>);
    }
    const defaultRenderItem = (item, index, arr) => {
        let { title, cover_picture, main_tag, slug } = item;
        return <ItemWrapper
            className={"item item-" + (typeof index == "undefined" ? "cover" : "thumbnails")}
            onClick={() => {
                navigate(`/${slug}`)
            }}
            xl={typeof index == "undefined" ? 12 : 6}
        >
            <div className="item-container" style={{
                background: `url(${URL_BASE_S3}${cover_picture})`
            }}>
                <span className="item-tag" style={{
                    backgroundColor: main_tag && main_tag.color
                }}>{main_tag && main_tag.name}</span>
                <div className="item-title">{title}</div>
            </div>
        </ItemWrapper>
    }
    const getItems = () => {
        let data = {}
        let count = 0;
        for (let i = 0; i < columns; ++i) {
            data[i] = data[i] || {};
            data[i]["children"] = data[i]["children"] || [];
            let maxColum = columns > 1 ? perColumn : dataSource.length;
            for (let j = 0; j < maxColum; ++j) {
                if (count < dataSource.length) {
                    data[i]["children"].push({
                        ...dataSource[count]
                    });
                }
                count++;
                console.log("Col: ", i, "Row: ", j)
            }
        }
        return Object.values(data);
    }
    useEffect(() => {
        let items = getItems();
        setItems(items);
    }, [dataSource]);
    return (
        <Wrapper {...props}>
            {dataSource.length > 0 && <Row xl={columns}>
                {
                    items.map((item, index, arr) => renderGridLayout(item, index, arr))
                }
            </Row>}
        </Wrapper>
    );
}
export default Grid;