import styled from 'styled-components';
import { List } from "antd"
const { Item } = List;
export const WrapperList = styled(List)`
    margin-top:40px;
`;
export const WrapperItem = styled(Item)`
    cursor:pointer;
    &.ant-list-item{
        transition: all 0.25s ease;
    }
    &.ant-list-item:hover{
        transform: translateY(-3px);
    }
    & .item-container{
        display:flex;
        min-width: 100px!important;
        border-radius: 8px;
    }
    & .item-image{
        width: 100%!important;
        background-size: contain!important;
        background-repeat: no-repeat!important;

        -webkit-box-shadow: 7px 45px 92px -33px rgba(0,0,0,0.45);
        -moz-box-shadow: 7px 45px 92px -33px rgba(0,0,0,0.45);
        box-shadow: 7px 45px 92px -33px rgba(0,0,0,0.45);
    /*  border-radius: 20px; */
    }
`;