import React from "react";
import { Layout, Carousel, Tag } from "antd";
import { Wrapper, ItemWrapper, BannerContentTwo } from "./Styles";
import { isMobile } from "react-device-detect";

const { Header } = Layout;

let items = [
    {
        // legend: "Pediatra",
        title: "DE ESTE MES",
        description: "LOS DESTINOS MS TOP",
        image_path: "/images/Destinos.png",
        thubmnail: "/images/banner-min.png"
    }
];

const Item = ({ image_path, thubmnail, legend, title, description, ...props }) => {
    return <ItemWrapper style={{
        background: `url(${isMobile && thubmnail ? thubmnail : image_path})`
    }}>
        <BannerContentTwo>
            {legend && <div className="lenged-container">
                <Tag>{legend}</Tag>
            </div>}
            {description && <div className="description-container">
                <h2>{description}</h2>
            </div>}
            {title && <div className="title-container">
                <h1>{title}</h1>
            </div>}
        </BannerContentTwo>
    </ItemWrapper>
}
const BannerTwo = ({ dataSource = items, ...props }) => {
    return (<Wrapper>
        <Carousel>
            {
                dataSource.map((item, index) => (<Item key={index} {...item} />))
            }
        </Carousel>
    </Wrapper>)
}
export default BannerTwo;