import React, { useState, useEffect } from 'react';
import Banner from '../../components/banner/Banner';
import { getService } from "../../services/";
import { Wrapper } from "./Styles";
import { Layout, message, Row, Col, Tag, Avatar, Divider, Button, Icon, Dropdown, Menu } from "antd";
import Categorylist from '../../components/category-list/CategoryList';
import ArticleList from "../../components/articles-list/ArticlesList";
import GuidesList from '../../components/guides-list/GuidesList';
import Grid from "../../components/grid/Grid";
import CardCarousel from "../../components/card/CardCarousel";
import BannerTwo from '../../components/banner/BannerTwo';
import CardList from '../../components/card/CardList';
import Tags from '../../components/tags/Tags';
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";
import { CHANGE_ENTITY } from '../../constants/actionTypes';
import { DEFAULT_IMAGE_COVER, URL_S3 } from '../../constants/';
import Loader from '../../components/loader/';
import SearchField from "../../components/search-field/SearchField";
import { Router, Link, navigate } from "@reach/router"
import qs from "querystring";
import { isMobile } from "react-device-detect";

const Home = ({ t, id, city_id, reference = "cities", item, ...props }) => {
  const dispatch = useDispatch();
  const [record, setRecord] = useState({});
  const [loading, setLoading] = useState(true);
  const [cover, setCover] = useState();
  const [entity_id, setEntityId] = useState(props.entity_id);
  const [image_top, setImageTop] = useState();
  const [filterDefaultValues, setFilterDefaultValues] = useState();
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const entity = useSelector(({ auth }) => {
    return auth.entity;
  });
  const configuration = useSelector(({ auth }) => {
    return auth.configuration
  })
  const getEntity = () => {
    const service = getService("entities");
    setLoading(true);
    service.get(entity_id)
      .then(entity => {

        window.localStorage.setItem("entity", JSON.stringify(entity))
        setLoading(false);
        dispatch({
          type: CHANGE_ENTITY,
          entity
        });
      })
      .catch(err => {
        setLoading(false);
      });
  }
  /*const handleClick = (id) => {
    navigate(`/city-packs/${id}`);
  }
  const getCities = () => {
    if (reference) {
      const service = getService(reference);
      service.get(id)
        .then(record => {
          setRecord(record);
        })
        .catch(err => {
          message.error(err.message)
        })
    }
  }
  useEffect(() => {
    getCities();
  }, [id]) */
  useEffect(() => {
    let { search } = props.location;
    if (search) {
      let query = qs.parse(search.replace(/\?/g, ""));
      if (query.entity_id)
        setEntityId(query.entity_id);
    } else {
      setEntityId(1);
    }
  }, []);
  useEffect(() => {
    if (entity_id)
      getEntity();
  }, [entity_id]);
  useEffect(() => {
    if (entity && !loading) {
      let cover = entity.gallery ? entity.gallery.gallery_media && entity.gallery.gallery_media.length > 0 && entity.gallery.gallery_media[0].path
        && `${URL_S3}/${entity.gallery.gallery_media[0].path}`
        : DEFAULT_IMAGE_COVER;
      let image_top = entity.gallery ? entity.gallery.gallery_media && entity.gallery.gallery_media.length > 0 && entity.gallery.gallery_media[1].path
        && `${URL_S3}/${entity.gallery.gallery_media[1].path}`
        :
        "/images/Destinos.png";
      setCover(cover)
      setImageTop(image_top)
    }
  }, [entity]);
  if (loading) return <Loader entity={entity} />
  return (<Wrapper configuration={{
    primary_color: entity && entity.primary_color || "var(--color-primary);",
    secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
    text_color: entity && entity.text_color || "var( --color-gray-dark)"
  }}>
    <Banner
      dataSource={[{
        // legend: "Pediatra",
        title: t('home.banner.title'),
        description: t('home.banner.description'),
        image_path: cover,
        thubmnail: cover
      }]}
    />
    <Row className="section section-search" type="flex" justify="center" align="center" gutter={10}>
      <Col className="section-container" xl={10} lg={16} md={16} xs={22} >
        <SearchField
          placeholder={t("search.Text.Input")}
          reference="packs"
          open={false}
          source={`name_${locale}`}
        />
      </Col>
    </Row>
    <Row
      className="section-container"
      justify="center" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
      <Col className="section section-category" xl={20} lg={24} md={24} xs={24}>
        <div className="categories">
          <Tags
            filterDefaultValues={{
              entity_id
            }}
            reference="categories-packs"
            /* dataSource={categories} */ />
        </div>
      </Col>
    </Row>
    <Row
      className="section-container top-packs"
      justify="center" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
      <Col className="section section-headline" xl={20} lg={24} md={24} xs={24}>
        <Divider>
          <span>{t("home.topPacks")}</span>
        </Divider>
        <CardCarousel
          filterDefaultValues={{
            entity_id,
            $limit: 10,
            //type: "top-10",
            $sort: {
              createdAt: -1
            }
          }}
          reference="packs" />
      </Col>
    </Row>
    <Row
      className="section-container new-packs"
      justify="center" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
      <Col className="section section-headline" xl={20} lg={24} md={24} xs={24}>
        <Divider>
          <span>{t("home.newPacks")}</span>
        </Divider>
        {<CardCarousel
          filterDefaultValues={{
            entity_id,
            $limit: 10,
            $sort: {
              createdAt: -1
            }
          }}
          reference="packs" />}
      </Col>
    </Row>
    <Row
      className="section-container"
      justify="center"
      type="flex"
      align="top"
      gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]} >
      <Col className="section section-headline full-width" xl={24}>
        <Banner
          titlePosition={"top"}
          dataSource={[{
            title: t("home.mostTopBanner.title"),
            description: t("home.mostTopBanner.description"),
            image_path: image_top,
            thubmnail: image_top
          }]} />
      </Col>
    </Row>
    <Row
      className="section-container"
      justify="center"
      type="flex"
      align="top"
      gutter={[{ xs: 4, sm: 16, md: 24 }, { xs: 4, sm: 16, md: 24 }]} >
      <Col className="section section-headline" xl={20} lg={24} md={24} xs={24}>
        <CardList

        />
      </Col>
    </Row>
    <Row
      className="section-container offers-packs"
      justify="center" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
      <Col className="section section-headline" xl={20} lg={24} md={24} xs={24}>
        <Divider>
          <span>{t("home.packsOffers")}</span>
        </Divider>
        {<CardCarousel
          filterDefaultValues={{
            entity_id,
            $limit: 10,
            //type:"offers",
            $sort: {
              createdAt: -1
            }
          }}
          reference="packs" />}
      </Col>
    </Row>
  </Wrapper>)
}
export default translate()(Home);