import React, { useEffect, useState } from "react";
import { Carousel, Col, Row, Card, Button, Avatar, Divider, Drawer, Skeleton, message, Tag } from "antd";
import { Link, navigate } from "@reach/router";
import { Wrapper, WrapperEmpty, WrapperCard, Wraperdrawer } from "./Styles"
import { getService } from "../../services/services";
import { DEFAULT_IMAGE, URL_PLACEHOLDER, URL_S3 } from "../../constants/";
import { useSelector, useDispatch } from "react-redux";
import * as Helper from "../../utils/";
import * as actionTypes from "../../constants/actionTypes";
import { translate } from "react-polyglot";
import qs from "querystring";
let { money } = Helper;
const { Meta } = Card;
let settings = {
  dots: true,
  infinite: false,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 4,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
        infinite: true,
        dots: true
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        initialSlide: 2
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};

const CardList = ({ t, actions = {}, allowSelect = true, extra, refinement, filterDefaultValues = {}, slidesToShow = 4, reference, ...props }) => {
  const dispatch = useDispatch();
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const show = useSelector(({ auth }) => {
    return auth.show;
  });
  const cart = useSelector(({ auth }) => {
    return auth.cart || [];
  });
  const user = useSelector(({ auth }) => {
    return auth.user;
  });
  const entity = useSelector(({ auth }) => {
    return auth.entity;
  });
  const [showdrawer, seShowtDrawer] = useState(false);
  const [onClose, setOnClose] = useState(false)
  const [visible, setVisible] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [loading, setLoading] = useState(true);
  const [dataSource, setDataSource] = useState([]);
  const getData = () => {
    settings = {
      ...settings,
      slidesToShow
    }
    if (reference) {
      const service = getService(reference);
      setLoading(true);
      let data = Array.from(Array(slidesToShow).keys());
      setDataSource(data);
      service.find({
        query: {
          ...filterDefaultValues
        }
      })
        .then(({ data }) => {
          setDataSource([...data, actions.create && {
            type: "empty"
          }].filter(it => (typeof it != "undefined")))
          setLoading(false);
        })
        .catch(err => {
          setLoading(false);
          message.error(err.message);
        })
    }
  }
  const handleSubmit = () => {
    setLoading(loading => (!loading));
    setVisible(false);
  }
  const handleRemove = (id) => {
    /* service.remove(id)
      .then(res => {
        setRefresh(refresh => !refresh);
        message.success("Paquete eliminado")
      })
      .catch(err => {
        message.error(err.message);
      }) */
  }
  const handleClick = () => {
    navigate(`/view-all/?${qs.stringify(filterDefaultValues)}`)
  }
  const defaultRenderitem = (item, index) => {
    let record = item;
    if (refinement && record.type != "empty") record = refinement(record);
    if (record.type == "empty")
      return <WrapperEmpty  configuration={{
        primary_color: entity && entity.primary_color || "var(--color-primary);",
        secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
        text_color: entity && entity.text_color || "var( --color-gray-dark)"
       }} >
        <div className="empty">
          <div className="title-empty"><span><h1>{t("pack.card.titleAdd")}</h1></span>
            <Link to={"/reservations"}><span>{t("pack.card.descriptionAdd")}</span></Link>
          </div>
        </div>
      </WrapperEmpty>;

    let photos = record.gallery && record.gallery.gallery_media ? record.gallery.gallery_media.length : 0;
    let image = record.gallery ? record.gallery.gallery_media && record.gallery.gallery_media[0] &&
      record.gallery.gallery_media[0].path ?
      `${URL_S3}/${record.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
      : DEFAULT_IMAGE;

    let category_cover = record.category_pack ?
      record.category_pack.image_cover ?
        `${URL_S3}/${record.category_pack.image_cover}` :
        URL_PLACEHOLDER :
      URL_PLACEHOLDER;

    let description = record.description_es || record.description_en;
    let isSelected = (typeof cart.find(it => (it.id == record.id)) != "undefined");
    let defaultActions = [
      allowSelect && <Button onClick={() => {
        if (!isSelected) {

          if (!cart.find(it => (it.id == record.id)))
            dispatch({
              type: actionTypes.ADD_TO_CART,
              cart: [...cart, record]
            });
          if (cart.length == 0) {
            dispatch({
              type: actionTypes.SHOW_CAR,
              show: true
            });
          }
        }
        if (isSelected)
          dispatch({
            type: actionTypes.ADD_TO_CART,
            cart: cart.filter(it => (it.id != record.id))
          });
      }}
        className="btn-add-to-reservation"
        block
        type={!isSelected ? "primary" : "danger"}
      >
        {
          isSelected ? t("global.selectedText") :
            t("packs.addToReservation")
        }
      </Button>
    ];
    extra = defaultActions;
    if ((typeof extra === 'boolean' && !extra) || (extra && !allowSelect))
      extra = undefined;

    if (loading) return <WrapperCard>
      <Skeleton
        paragraph={{
          rows: 3,
          width: 150,
          height: 100
        }}
        title={{
          width: 200,
          height: 200
        }}
        avatar={{
          shape: "square",
          size: "large"
        }}
        loading={loading}
        active={loading}
      /></WrapperCard>

    return <WrapperCard  configuration={{
      primary_color: entity && entity.primary_color || "var(--color-primary);",
      secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
      text_color: entity && entity.text_color || "var( --color-gray-dark)"
     }}
      color="red"
      loggedIn={(user && user.role)}
      photos={photos}>
      <Card
        className={`card-carousel`}
        hoverable={false}
        bordered={false}
        style={{ minWidth: 270 }}
        actions={extra}
        cover={
          <div
            className="cover">
            <img
              onClick={() => {
                navigate(`/pack/${record.id}`)
              }}
              src={image} />
            <div className="tools">
              {actions.remove && <Button
                onClick={() => handleRemove()}
                shape="circle"
                type="primar"
                icon="delete" />}
            </div>
          </div>
        }
      >
        <Meta
          title={
            <div className="title-container">
              <div className="categories">
                <Tag>{record.category_pack && record.category_pack.name}</Tag>
              </div>
              <div className="cardtitle">
                <h2>{record[`name_${locale || window.localStorage.getItem("locale") || "en"}`]}</h2>
              </div>
            </div>
          }
          description={
            <div className="description">
              {record.city && <div className="city-container">
                <Avatar shape="square" size="small" src={"/images/pin.svg"} />{" "}
                <span className="city-name">{record.city && record.city.name}</span>
              </div>}
              {user && <Divider dashed />}
              <div className="footer">
                {user && <div className="price-container">
                  <div className="label-price"><span>{t("packs.labelPrice")}</span></div>
                  <div className="price"><span>{money(record.price_cop)}</span></div>
                </div>}
                {user && <div className="buttons">
                  <Link to={`/pack/${record.id}`} >
                    <Button type="primary" shape="circle" icon="arrow-right" />
                  </Link>
                </div>}
              </div>
            </div>
          }
        />
      </Card>
    </WrapperCard >
  }
  useEffect(() => {
    if (props.dataSource)
      setDataSource([...props.dataSource, actions.create && {
        type: "empty"
      }].filter(it => (typeof it != "undefined")))
  }, [props.dataSource]);
  useEffect(() => {
    if (reference) {
      getData();
    }
  }, [reference]);
  useEffect(() => {
    if (!reference)
      setLoading(props.loading)
  }, [props.loading]);
  return (
    <Wrapper  configuration={{
      primary_color: entity && entity.primary_color || "var(--color-primary);",
      secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
      text_color: entity && entity.text_color || "var( --color-gray-dark)"
     }}>

      <Row type="flex" align="center" justify="center" gutter={[10, 10]}>
        <Col span={24} >
          <Carousel  {...settings} autoplay={props.autoplay || false}>
            {

              dataSource.map(props.renderItem || defaultRenderitem)}
          </Carousel>
        </Col>
        {loading && <Col span={24}>
          <div className="button-show">
            <Button active className="mybutton"
              onClick={() => handleClick()}
            >{t("packs.showAllText")}
            </Button></div>
        </Col>}
      </Row>
    </Wrapper>
  );
};

export default translate()(CardList);