import styled from 'styled-components';


export const Wrapper = styled.div`
:root{
    --color-primary:red!important;
}

    & .ant-carousel .slick-slide {
        min-height: 550px!important;
    }
    
    & .ant-card{
        background: #F9F2FF 0% 0% no-repeat padding-box!important;
        box-shadow: 4px 4px 8px #0000001f!important;
        border-radius: 8px!important;
        opacity: 1!important;
        margin: 0px 4px!important;
        overflow:hidden;
    }
    & .ant-carousel .slick-list .slick-slide.slick-active {
        height: 500px;
        padding: 2px
    }
    & .ant-card-body {
        padding: 14px 16px 16px 16px!important;
        overflow:hidden;
    }
    & .cardtitle{
        display: flex;
        justify-content: start;
        align-items: center;
    }
    & .cardtitle h2{
        font-size: 24px;
        font-family: 'Jost';
        font-weight: 600;
        letter-spacing: 0px;
        margin: 8px 0px;
        text-transform: capitalize;

        white-space: normal!important;
        white-space: normal!important;
        line-height: 1!important
        text-align:left!important;
        margin-bottom: 0px;
    }
    & .ant-card-meta-description {
        margin: 0px!important;
        display: flex;
    }

   /*  & .ant-col.ant-col{
    padding-top: 22px !important; 
    } */
    & .ant-carousel .slick-slide {
        text-align: center;
        background: #e2e4e808;
        line-height: 160px;
        overflow: hidden;   
    }
    & .ant-carousel .slick-slide h3 {
        color: #fff; 
    }
    & .ant-card-cover img{
        transition: transform .4s,-webkit-transform .4s;
        opacity:.8;
    }
    & .ant-card-cover:hover img{
        transform: scale(1.2);
    }
    & .ant-card-cover {
        height: auto !important;
        overflow: hidden;
        border-radius: 8px 8px 0px 0px!important;
        overflow:hidden;
    }
    & .ant-carousel .slick-slide .cover img {
        height: 270px
    }
    &  .ant-card-cover .cover img {
        object-fit: cover;
    }
    /* & .ant-card-meta-detail {
        display: table !important;
    } */
    & .style-footer {
        display: flex;
        font-family: 'Jost';
        font-weight: 600;
        font-size: 11px;
        color:#AD42FF;
    }
    & .style-price {
        display: flex;
        font-family: 'Jost';
        font-weight: 600;
        font-size: 22px;
        color: var(--color1);   
    }
    & .style-button {
        display: flex;
        justify-content: flex-end;
        margin-top: -34px;
    }
    & .ant-btn-icon-only {
        align-content: center!important;
        display: inline-grid!important;
    }

    & .tools{
        position: absolute;
        right: 0;
        top: 0;
        padding: 4px;
        margin: 2px;
    }
    & .button-show .ant-btn{

        color: var(--color-primary)!important;
        background-color: #fff;
        border-color: var(--color-primary)!important;
        font-weight: 600;
        font-family: 'Jost';
        padding: 4px 20px;
        height: 40px;
        text-align: center;

    }
    & .button-show{
        text-align:center;
        margin-bottom:20px!important;
        margin-top:16px!important;
    }
    & .ant-carousel .slick-dots li.slick-active button {
        background: var(--color-secundary)!important;
        box-shadow: 2px 2px 2px #be5cff!important;
        border: 1px solid var(--color-secundary)!important;
    }
    & .ant-carousel .slick-dots li button {
        width: 14px!important;
        height: 14px!important;
        opacity: 1!important;
        border-radius: 50%!important;

        background: #e8e8e8!important;
        box-shadow: 2px 2px 2px rgb(0 0 0/ 15%)!important;
        border: 1px solid rgb(0 0 0 / 15%)!important;
    }
    ${({ configuration }) => {
        return `
            & .button-show .ant-btn{
                border-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
        
        `;
    }}
 
`;
export const WrapperMyCard = styled.div`
    /* fvargas */
    & .card-title-container > div{
        margin:8px 0px 0px 0px!important;
    }
    & .ant-card{
        background: #F9F2FF 0% 0% no-repeat padding-box!important;
        box-shadow: 0px 3px 46px #00000040!important;
        border-radius: 5px!important;
        width: 340px;
        border: 1px solid #A734FF1A!important;
    }
    & .ant-card-body {
        padding-bottom: 0px!important;
    }
    & .ant-card .card-title h2{
        line-height: 1!important;
        white-space: normal!important;
        font-weight: 600!important;
        margin-bottom: 0!important;
    }
    & .ant-tag{
        cursor:pointer!important;
        border: 0px!important;
        border-radius: 28px !important;
        padding: 8px !important;
        min-width: 80px;
        text-align: center;
        font-size: 14px !important;
        background: #F4E6FF 0% 0% no-repeat padding-box!important;
        color: var(--color-primary);
        font-weight: 600;
        font-size: 16px!important;
        text-transform: capitalize!important;
    }
    & .card-city .ant-avatar{
        margin-right: 4px!important;
    }
    & .card-city .ant-avatar > img {
        object-fit: contain!important;
    }
    & .card-city .avatar {
        display: flex;
        justify-content: start;
        align-items: center;
    }
   /* description */
   & .card-description-container{
        margin: 20px 0px;
   }
   & .card-description-container .card-label span{
       font-size:14px;
       font-weight:600;
       text-transform: uppercase;
       color: var(--color-secundary)!important;
   }
   & .card-description-container .card-value span{
       font-size:24px;
       font-weight:600;
       text-transform: uppercase;
       color: var(--color-primary)!important;
   }
   & .card-price-container{
       margin:4px 0px;
   }
   &  .ant-card-actions{
       border-top:0px!important;
       background: #F9F2FF 0% 0% no-repeat padding-box!important;
       border-radius: 0px 0px 8px 8px!important;
   }
   & .btn-add-to-reservation{
       width:60%!important;
    }
    & .btn-add-to-reservation span{
        font-size: 18px!important;
        font-weight: 500!important;
    }
    /* fvargas */
    ${({ configuration }) => {
        return `
            & .ant-tag {
                /* border-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important; */
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .card-description-container .card-label span{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .card-description-container .card-value span{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .styles-dpoins >span{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .btn-add-to-reservation{
                background-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
                border-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
        
        `;
    }}
`;

export const WrapperCardsPack = styled.div`
/*  & .ant-card-cover img{
    transition: transform .4s,-webkit-transform .4s;
    opacity:.8;
}
& .ant-card-cover:hover img{
    transform: scale(1.2);
} */

    & .ant-list {
        
        /* width: fit-content !important; */
    }
   /*  & .ant-list-item-meta-avatar img{
        transition: transform .4s,-webkit-transform .4s;
    }
    & .ant-list-item-meta-avatar:hover img{
        transform: scale(1.2);
    } */

/* responsi */
@media(max-width:576px){
     .ant-list-item-meta-avatar img{
        max-width: 100px;
        max-height: 100px;
    }
    .ant-col.ant-col-xs-24.ant-col-sm-12.ant-col-md-8.ant-col-lg-6.ant-col-xl-6 {
        display: flex;
        justify-content: center;
    }
.ant-list-item-meta-content {
        display: block;
        padding-top: 10px!important;
        margin-top: 10px!important;
    }
    .ant-list-item-meta {
        border-radius: 9px!important;
        max-width: 244px;
    }
}
/* sm */
@media(max-width:768px){
    .ant-list-item-meta-avatar img{
        max-width: 114px;
        max-height: 114px;
    }
.ant-list-item-meta-content {
    padding-top: 10px!important;
    margin-top: 17px!important;
    }
    .ant-list-item-meta {
        border-radius: 9px!important;
        max-width: 255px;
       
    }
}

    & .ant-list-item-meta-avatar img{
            width: 140px!important;
            height: 135px!important;
            object-fit: cover;
    }
    & .ant-list-item-meta-avatar {
        margin-right: 20px!important;
        margin-left: 6px!important;
    }
    
    & .ant-list-item-meta {
        
        border-radius: 9px!important;
        background: #bfbfbf1f!important;
    }
    & .ant-list * {
        border-radius: 5px!important;
        cursor: pointer!important;
    }
    & .ant-spin-container {
        padding-top: 21px;
    }
    & .ant-list-item{
        display: flex !important;
        max-width: 100%;
        margin-bottom: 40px!important;
        padding-top: 10px!important;
        padding-bottom: 4px!important;
        border-bottom: none
    }
    & .ant-list-item-meta-content {
        
        display: block;
        padding-top: 20px;
        margin-top: 20px;
    }
    & .ant-list-item-meta-title {
        margin-bottom: -9px;
    }
`;
