import React, { useEffect, useState } from "react";
import { WrapperCardsPack } from "./Styles"
import { Carousel, Card, Icon, Row, Col, Divider, Button, Avatar, Layout, List, message } from "antd";
//import { Link } from "react-router-dom";
import { Router, Link } from "@reach/router"
import Grid from "antd/lib/card/Grid";
import { navigate } from "@reach/router";
import { useSelector, useDispatch } from "react-redux";
import { translate } from 'react-polyglot';
import { getService } from "../../services";
import { DEFAULT_IMAGE_COVER, URL_PLACEHOLDER, URL_S3 } from "../../constants/";
const { Meta } = Card;
const CardPacks = ({ t, record, reference = "cities", filterDefaultValues, item, ...props }) => {
  const [dataSource, setDataSource] = useState([]);
  const [loading, setLoading] = useState(true);
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const handleClick = (id) => {
    navigate(`/city-packs/${id}`);
  };
  const defaultRenderItem = (item, index) => {
    let cover = item.cover ? `${URL_S3}/${item.cover}` : DEFAULT_IMAGE_COVER;
    return (<List.Item>
      <List.Item.Meta
        avatar={<img src={cover} onClick={() => handleClick(item.id)} />}
        title={<div><h5>{item.name}</h5></div>}
        description={<div> <Button
          onClick={() => handleClick(item.id)}
          type="ghost" key="city-pack-details">{t("cardlist.TextBotton.view")}</Button></div>}
        
      />
    </List.Item>)
  };
  const getData = () => {
    if (reference) {
      const service = getService(reference);
      service.find({
        query: {
          ...filterDefaultValues
        }
      })
        .then(({ data }) => {
          setDataSource(data);
        })
        .catch(err => {
          message.error(err.message)
        })
    }
  }
  useEffect(() => {
    getData();
  }, []);
  return (
    <WrapperCardsPack>
      <Row type="flex" aligntop="center" justify="center" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
        <Col className="container-section-list" span="24" >
          <List
            //itemLayout="horizontal"
            dataSource={dataSource}
            grid={{
              gutter:4,
              xs: 1,
              sm: 2,
              md: 3,
              lg: 4,
              xl: 4,
            }}
            renderItem={props.renderItem || defaultRenderItem}

          />
        </Col>
      </Row>
    </WrapperCardsPack>

  )
};
export default translate()( CardPacks);
